@font-face {
    font-family: "Roboto Mono Thin";
    src: url("../fonts/Roboto Mono Thin.ttf");
}

a {
    color: #FFFFFF;
    text-decoration: none;
}

article {
    display: flex;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
    width: max-content;
}

body {
    background-color: #0e1212;
    color: #FFFFFF;
    font-family: "Roboto Mono Thin";
    font-weight: bold;
    height: calc(95vh - 80px);
    margin: 0px;
    padding: 0px;
}

footer {
    & > .author {
        & > i { color: #e92a2a; }

        font-size: 15px;
    }

    & > .links > a {
        color: #682ae9;
        font-size: 20px;
    }

    align-items: center;
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-left: auto;
    margin-right: auto;
    padding: 10px;
    width: max-content;
}

header {
    & > div {
        align-items: center;
        display: flex;
        gap: 10px;
    }

    color: #682ae9;
    display: flex;
    font-size: 15px;
	justify-content: space-between;
    padding: 10px;
}